.contacts {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;

    .header__nav {
        width: 100%;
        padding: 0;
    }

    .container {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        gap: 15px;
        width: 800px;
        padding: 30px;
        border-radius: 10px;
        background-color: #fff;
        box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);

        strong {
            background-color: aquamarine;
            text-align: center;
            padding: 10px;
        }

        p {
            margin: 0;
            padding: 0;
            font-size: 12px;
            line-height: 22px;

            span {
                font-size: 14px;
                font-weight: bold;
            }
        }

        .nav-btn {

            a {
                text-decoration: none;
                font-size: 12px;
                color: rgb(182, 182, 182);
                transition: color 0.2s ease-in-out;
            }
            
            &:hover {
                color: green;
            }
        }
    }

    .footer__nav {
        width: 100%;
        padding: 0;
    }
}

